.containar {
    display: flex;
    height: 90vh;
    width: 100%;

    /* background-color: #dddd; */
    /* background-image: url("shoppingcarimg.jpg"); */
    /* background-size: cover; */
}

.productall {
    /* border: 2px solid black; */
    height: 100%;
    width: 100%;
    overflow: auto;
}

.productall::-webkit-scrollbar {
    display: none;
}

.search {
    text-align: center;
    /* margin: 30px; */

}

.input {
    border-radius: 20px 0px 0px 20px;
    /* background-color: #66BFBF; */
    /* background: linear-gradient(45deg, #00b5ff, #3f78da); */
    /* background-color: #31c5ff; */
    background-color: rgb(162, 112, 212);
    /* background: linear-gradient(250deg, hsla(294, 97%, 56%, 0.746), #2e79f9f9); */

    color: black;
    font-size: 30px;

}

::placeholder {
    color: black;
}

.input:hover {
    /* background-color: #DDDDDD; */
    background-color: white;
    /* background: linear-gradient(45deg, #94dffd, #6bc5ec2e); */
    /* background: linear-gradient(100deg, #ffb700, #fcfdfd 90%, #ffb700 10%); */
    /* background: linear-gradient(250deg, hsla(294, 97%, 56%, 0.746), #2e79f9f9); */
    transition: all 0.5s ease;
    color: black;


}

.button {
    border-radius: 0px 20px 20px 0px;
    /* background-color: #66BFBF; */
    /* background: linear-gradient(45deg, #00b5ff, #005eff); */
    /* background-color: #31c5ff; */
    /* background-color: #ffb700; */
    background: linear-gradient(250deg, hsla(294, 97%, 56%, 0.746), #2e79f9f9);
    background-color: rgb(162, 112, 212);
    font-size: 29px;
}

.products {
    margin-top: 30px;
    text-align: center;
    margin-left: -35px;
}

.productimg {
    width: 120px;
    height: 110px;
    filter: drop-shadow(15px 15px 15px);

}

.ProductHome-list {
    align-items: center;
    border-radius: 20px;
    margin: 8px;
    padding: 0px 5px 7px 5px;
    font-weight: 600;
    /* background: linear-gradient(130deg, hsla(294, 97%, 56%, 0.746) 10%, #2e79f9f9)90%; */
    /* background-color: rgba(6, 191, 175, 255); */
    /* background-color: #DDDDDD; */
    /* background: linear-gradient(100deg, #ecc9f1, rgb(147, 126, 206)); */
    background-color: #31c5ff;
    /* background-color: #4fcde1; */
    /* background-color: rgba(233, 175, 0, 255); */
    /* background-color: #a23147; */
    /* background: linear-gradient(130deg, #ffb700 10%, #fcfdfd 50%, #ffb700 40%); */
    border: 2px solid black;
}

.ProductHome-list:hover {
    color: black;
    /* background-color: #4fcde1; */
    background: linear-gradient(45deg, #c2cacd, #DDDDDD);
}

.ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    list-style: none;
    gap: 10px;
}





.offer {
    text-decoration: line-through;
}


.rating {
    font-size: 20px;
    border-radius: 15px;

}



.product-viewbutton {
    border-radius: 10px;
    font-size: 25px;

}

.product-viewbutton:hover {
    background: linear-gradient(45deg, #00b5ff, #005eff);

}

.Product-buttons {
    display: flex;
    justify-content: space-around;
}

.Addbuttonc {
    border-radius: 10px;
    font-size: 25px;
    background-color: #DDDDDD;
}

.Addbuttonc:hover {
    background: linear-gradient(45deg, #00b5ff, #005eff);

}



.cardline1 {
    border-bottom: 2px solid black;
    text-align: center;
    margin: 8px;
    padding-bottom: 20px;

}


.Bag {
    border-radius: 5px;
    margin: 10px;
    font-size: 25px;
    border: none;
    /* background-color: #DDDDDD; */
}

.count-Bag {
    max-width: 50px;
    height: 20px;
    border: none;
    font-size: 30px;
    /* background-color: hsla(294, 96%, 82%, 0.746); */

}




@media screen and (max-width:600px) {
    .ul {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

    }

}