@media screen and (max-width:700px) {

    .Mobileicon {
        display: flex;
    }

    .title {
        display: flex;

    }

    .link {
        display: none;
    }

    .Mobilelink {
        position: absolute;
        display: block;
        list-style: none;
        background-color: #ddd;
        /* background: linear-gradient(45deg, #00b5ff, #005eff); */
        left: 0px;
        top: 75px;
        transition: all 0.5s ease-out;
        width: 100%;
        margin-top: -10PX;
        text-align: center;

    }

    .Mobilelink li {
        margin: 25px;

    }

    .home,
    .about,
    .product,
    .signup {
        color: black;
        text-align: center;
        width: 100%;
        transition: all 0.5s ease;

    }

    .Mobileicon {
        display: block;
        position: absolute;
        font-size: 20px;
        color: white;
        background-color: black;
        outline: none;
        top: 15px;
        right: 25px;

    }
}