.home-container {
    height: 90vh;
    width: 100%;
    /* background: linear-gradient(250deg, hsla(294, 97%, 56%, 0.746), #2e79f9f9); */
    background: linear-gradient(100deg, #ecc9f1, rgb(147, 126, 206), #e5b5ebf6 30%);

    display: flex;
    word-wrap: wrap;
    gap: 30px;
    justify-content: space-between;
}

.home-img-heater {
    display: flex;
    justify-content: center;
    align-items: center;
}

.advac-img {
    border-radius: 55%;
    background: #a975b3;
    /* box-shadow: 5px 5px 14px #533958,
        -5px -5px 14px #ffb1ff; */
    align-items: center;
}

.home-taitel-list-all {
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-taitel-list {
    text-align: center;
}

.home-advac1 {
    color: #a23147;
    /* text-shadow: 4px 4px 4px black; */

}

.home-advac2 {
    color: white;
    font-size: 70px;
    text-shadow: 4px 4px 4px black;
}

.home-advac3 {
    color: #a23147;
}

.home-advac4 {
    font-weight: bolder;
}

.home-button {
    text-decoration: none;
    padding: 1rem 2rem;
    font-weight: 700;
    background: black;
    color: wheat;
    border-radius: .5rem;
    border-bottom: 2px solid rgb(253, 150, 5);
    border-right: 2px solid rgb(253, 150, 5);
    border-top: 2px solid black;
    border-left: 2px solid black;
    transition-duration: 1s;
    transition-property: border-top,
        border-left,
        border-bottom,
        border-right,
        box-shadow;
}

.home-button:hover {
    border-top: 2px solid blueviolet;
    border-left: 2px solid blueviolet;
    border-bottom: 2px solid rgb(238, 103, 238);
    border-right: 2px solid rgb(238, 103, 238);
    box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px;
}

@media screen and (max-width:600px) {
    .home-container {
        display: grid;
        word-wrap: wrap;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }

    .advac-img {
        width: 500px;
    }
}