.cartcontainar {
    height: 89vh;
    width: 40%;
    background-color: #dddd;
    border: 2px solid black;
    overflow: auto;
}

.cartcontainar::-webkit-scrollbar {
    display: none;
}

.Header {
    display: flex;
    background: linear-gradient(250deg, hsla(294, 92%, 51%, 0.746), #2e79f9f9);
    justify-content: space-around;
    align-items: center;
}


.taitle {
    justify-content: center;
    margin: 10px;
    padding: 10px;
}

.cart-icon {
    margin-left: 25%;
    font-size: 30px;


}

.Bag-x {
    font-size: 20px;
    background-color: hsla(294, 76%, 61%, 0.746);
    font-weight: bolder;
}

.count {
    max-width: 50px;
    height: 20px;
    border: none;
    font-size: 30px;
    background-color: hsla(294, 76%, 61%, 0.746);

}

.qut {
    max-width: 50px;
    border: none;
    font-size: 30px;
    background-color: #31c5ff;

    /* background-color: #0762ffef; */
    /* background: linear-gradient(0deg, #00b5ff, #005eff); */

}

.cartline1 {
    display: flex;
    justify-content: center;
}

.totalchag {
    font-weight: bolder;
}

.totalAll {
    border: 2px solid black;
}

.total-price {
    text-align: right;

}


.Addcart {
    display: flex;
    list-style: none;
    border: 2px solid black;
    border-radius: 20px;
    padding: 10px;
    margin-top: 10px;
    /* margin-left: -25px; */
    /* margin-right: 10px; */
    /* justify-content: space-between; */
    /* background-color: #66BFBF; */
    /* background: linear-gradient(45deg, #00b5ff, #005eff); */
    background-color: #31c5ff;
}

.cart-delete-button {
    border: none;
    /* background-color: #00b5ff; */
    /* background: linear-gradient(10deg, #00b5ff, #005eff); */
    background-color: #31c5ff;

    >i {
        font-size: 25px;
        color: red;
    }
}

.cart-newimg {
    height: 50px;
    width: 50px;
}

@media screen and (max-width:600px) {
    .cartcontainar {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }

}